<template>
  <Drawer :width="300" placement="right" :closable="false" :styles="{ padding: '24px' }" v-model="visible">
    <Divider size="small"></Divider>
    <SettingItem v-if="!isTopMenu">
      <Row :gutter="24">
        <Col v-for="item in siderStyles" :span="12" :key="item.title">
          <Tooltip transfer placement="top" :content="item.title">
            <div :class="themeClasses(item.type, item.theme)" @click="setSiderTheme(item.theme)">
              <ThemeBlock :theme="item.block" />
            </div>
          </Tooltip>
        </Col>
      </Row>
    </SettingItem>
    <SettingItem>
      <Row :gutter="24">
        <Col v-for="item in headerStyles" :span="8" :key="item.title">
          <Tooltip transfer placement="top" :content="item.title">
            <div :class="themeClasses(item.type, item.theme)" @click="setHeaderTheme(item.theme)">
              <ThemeBlock :theme="item.block" />
            </div>
          </Tooltip>
        </Col>
      </Row>
    </SettingItem>

    <Divider size="small"></Divider>
    <SettingItem>
      <Row :gutter="24">
        <Col v-for="item in navModeList" :span="8" :key="item.title">
          <Tooltip transfer placement="top" :content="item.title">
            <div :class="navModeClasses(item.type)" @click="setLayoutMode(item.type)">
              <ThemeBlock :theme="item.block" />
            </div>
          </Tooltip>
        </Col>
      </Row>
    </SettingItem>

    <Divider size="small"></Divider>
    <SettingItem
      title=""
      :modelValue="headerWidth"
      :disabled="!isTopMenu || !isDesktop"
      :tipTitle="isDesktop ? ` [  ]` : ``"
      :radio-action="true"
      :radio-option="[
        { label: `fixed`, value: `` },
        { label: `fluid`, value: `` }
      ]"
      @on-change="setHeaderWidth"
    />
    <SettingItem
      title=""
      :tipTitle="isDesktop ? ` [  ]` : ``"
      :disabled="!isTopMenu || !isDesktop"
      :modelValue="contentWidth"
      :radio-action="true"
      :radio-option="[
        { label: `fixed`, value: `` },
        { label: `fluid`, value: `` }
      ]"
      @on-change="setContentWidth"
    />
    <SettingItem
      title=""
      :modelValue="siderFix"
      :disabled="isTopMenu && !isMobile"
      :tipTitle="` [  ]`"
      @on-change="setSiderFix"
    />
    <SettingItem title="" :modelValue="headerFix" @on-change="setHeaderFix" />
    <SettingItem
      title=""
      :modelValue="headerStick"
      :disabled="isTopMenu || !headerFix"
      :tipTitle="` [ / ] + [  ]`"
      @on-change="setHeaderStick"
    />
    <SettingItem
      title=""
      :modelValue="headerHide"
      :disabled="!headerFix"
      :tipTitle="` [  ]`"
      @on-change="setHideHeader"
    />
    <Divider size="small"></Divider>
    <SettingItem
      title=""
      :modelValue="showSiderCollapse"
      :disabled="isTopMenu || isMobile"
      :tipTitle="` [  ]`"
      @on-change="setShowSiderCollapse"
    />
    <SettingItem
      title=""
      :modelValue="showReload"
      :disabled="isTopMenu"
      :tipTitle="` [  ]`"
      @on-change="setShowReload"
    />
    <SettingItem
      title=""
      :modelValue="showBreadcrumb"
      :disabled="isTopMenu"
      :tipTitle="` [  ]`"
      @on-change="setBreadcrumbNav"
    />
    <SettingItem
      title=""
      :modelValue="showBreadcrumbIcon"
      :disabled="isTopMenu || !showBreadcrumb"
      :tipTitle="` [  ]`"
      @on-change="setBreadcrumbNavIcon"
    />
    <SettingItem title="" :modelValue="showSearch" @on-change="setShowSearch" />
    <SettingItem title="" :modelValue="showLog" @on-change="setShowLog" />
    <SettingItem title="" :modelValue="showFullscreen" @on-change="setShowFullscreen" />
    <SettingItem title="" :modelValue="showNotice" @on-change="setShowNotice" />

    <Divider size="small"></Divider>
    <SettingItem title="" :modelValue="tabsNav" @on-change="setTabsNav" />
    <SettingItem
      :disabled="!tabsNav"
      :tipTitle="` [  ]`"
      title=""
      :modelValue="tabsNavStyle"
      :radio-action="true"
      :radio-option="[
        { label: `dot`, value: `` },
        { label: `card`, value: `` }
      ]"
      @on-change="setTabsNavStyle"
    />
    <SettingItem
      title=""
      :modelValue="tabsNavIcon"
      :disabled="!tabsNav || tabsNavStyle !== `card`"
      :tipTitle="!tabsNav ? ` [  ]` : tabsNavStyle !== `card` ? `` : ``"
      @on-change="setTabsNavIcon"
    />
    <SettingItem
      title=""
      :modelValue="tabsNavFix"
      :disabled="!tabsNav"
      :tipTitle="` [  ]`"
      @on-change="setTabsNavFix"
    />
    <SettingItem
      title=""
      :modelValue="tabsReload"
      :disabled="!tabsNav"
      :tipTitle="` [  ]`"
      @on-change="setTabsReload"
    />
    <SettingItem
      title=""
      :modelValue="tabSortable"
      :disabled="!tabsNav"
      :tipTitle="` [  ]`"
      @on-change="setTabSortable"
    />

    <Divider size="small"></Divider>
    <SettingItem title="" :modelValue="pageBreadcrumb" @on-change="setPageBreadcrumb" />
    <SettingItem title="" :modelValue="pageDescription" @on-change="setPageDescription" />

    <Divider size="small"></Divider>
    <SettingItem title="">
      <template #action>
        <Select :modelValue="routeAnimate" @on-change="setRouteAnimate" size="small" style="width: 90px">
          <Option value="null"></Option>
          <Option value="slider-right"></Option>
          <Option value="slide-bottom"></Option>
          <Option value="zoom-in"></Option>
          <Option value="zoom-out"></Option>
          <Option value="fade"></Option>
        </Select>
      </template>
    </SettingItem>
    <SettingItem title="">
      <template #action>
        <Select :modelValue="dialogType" @on-change="setDialogType" size="small" v-width="90">
          <Option value="drawer"></Option>
          <Option value="modal"></Option>
        </Select>
      </template>
    </SettingItem>
    <SettingItem title="" :modelValue="responsiveLayout" @on-change="setResponsiveLayout" />
    <SettingItem
      title=""
      :modelValue="menuAccordion"
      :disabled="!isSideMenu"
      :extraTip="``"
      @on-change="setMenuAccordion"
    />
    <SettingItem title="" :modelValue="dynamicTitle" @on-change="setDynamicTitle" />
    <SettingItem title="" :modelValue="copyright" @on-change="setCopyright" />
    <SettingItem title="" :modelValue="watermark" @on-change="setWatermark" />
    <SettingItem title="" :modelValue="colorWeak" @on-change="setColorWeak" />
    <SettingItem title="" :modelValue="colorGrey" @on-change="setColorGrey" />

    <Divider />
    <div :style="{ marginBottom: '24px' }">
      <Button long @click="resetSetting"></Button>
      <Alert type="warning" :style="{ marginTop: '24px' }">
        <template #desc></template>
      </Alert>
    </div>
  </Drawer>
</template>

<script>
  import startup from '@/config/startup'
  import SettingItem from './SettingItem'
  import ThemeBlock from '@/components/ThemeBlock'
  import { mixinStore, mixinDevice } from '@/utils/mixin'

  export default {
    components: {
      ThemeBlock,
      SettingItem
    },
    mixins: [mixinStore, mixinDevice],
    data() {
      return {
        prefixCls: `${this.$config.cssPrefix}setting`,
        visible: false,
        siderStyles: [
          {
            type: 'siderTheme',
            title: '',
            theme: 'dark',
            block: 'sider-dark'
          },
          {
            type: 'siderTheme',
            title: '',
            theme: 'light'
          }
        ],
        headerStyles: [
          {
            type: 'headerTheme',
            title: '',
            theme: 'dark',
            block: 'header-dark'
          },
          {
            type: 'headerTheme',
            title: '',
            theme: 'light'
          },
          {
            type: 'headerTheme',
            title: '',
            theme: 'primary',
            block: 'header-primary'
          }
        ],
        navModeList: [
          {
            type: 'sideMenu',
            title: '',
            block: 'sider-dark'
          },
          {
            type: 'topMenu',
            title: '',
            block: 'top-nav'
          },
          {
            type: 'mixMenu',
            title: '',
            block: 'mix-nav'
          }
        ]
      }
    },
    computed: {
      themeClasses() {
        const { prefixCls } = this
        return (type, theme) => [
          `${prefixCls}-radio`,
          {
            [`${prefixCls}-radio-checked`]: this.settingEqual(type, theme)
          }
        ]
      },
      navModeClasses() {
        const { prefixCls } = this
        return type => [
          `${prefixCls}-radio`,
          {
            [`${prefixCls}-radio-checked`]: type === this.layoutMode
          }
        ]
      }
    },
    methods: {
      resetSetting() {
        startup(true)
      }
    }
  }
</script>
