import { createApp } from 'vue'
import store from '@/store'

const app = createApp({})

/**
 * Action 
 * 
 *  -  action  v-action:[method] , 
 *    <i-button v-action:add ></a-button>
 *    <i-button v-action:delete></i-button>
 *    <a v-action:edit @click="edit(record)"></a>
 *
 *  - 
 *  -  pro 
 *
 *  @see https://github.com/sendya/ant-design-pro-vue/pull/53
 */
const action = app.directive('action', {
  created(el, binding, vnode) {
    const actionName = binding.arg
    const { roles } = store.getters
    const elVal = vnode.context.$route.meta.permission
    const permissionId = (Object.prototype.toString.call(elVal) === '[object String]' && [elVal]) || elVal
    roles.permissions.forEach(p => {
      if (!permissionId.includes(p.permissionId)) {
        return
      }
      if (p.actionList && !p.actionList.includes(actionName)) {
        ;(el.parentNode && el.parentNode.removeChild(el)) || (el.style.display = 'none')
      }
    })
  }
})

export default action
